.main {
    background-color: white;
    padding: 1em;
    padding-bottom: 1.1em;
    border-radius: 15px;
    margin: 1em;
    height: calc(100vh - 260px);
    // 设置滚动条
    overflow-y: auto;
    .loader:hover {
        cursor: pointer;
        background-color: #93acf0;
    }

    .loader {
        display: flex;
        flex-direction: row;
        height: 4em;
        padding-left: 1em;
        padding-right: 1em;
        transform: rotate(180deg);
        justify-content: right;
        border-radius: 10px;
        transition: .4s ease-in-out;
        .loading {
            display: flex;
            margin-top: 1em;
            margin-left: 0.3em;

            .load:nth-child(1) {
                animation-delay: 0.2s;
            }

            .load:nth-child(2) {
                animation-delay: 0.4s;
            }

            .load:nth-child(3) {
                animation-delay: 0.6s;
            }

            .load {
                width: 2px;
                height: 33px;
                background-color: #6f91ee;
                animation: 1s move6 infinite;
                border-radius: 5px;
                margin: 0.1em;
            }
        }

        .albumcover {
            position: relative;
            margin-right: 1em;
            height: 40px;
            width: 40px;
            background-color: rgb(233, 232, 232);
            align-self: center;
            border-radius: 5px;
        }

        .play {
            position: relative;
            left: 0.35em;
            height: 1.6em;
            width: 1.6em;
            clip-path: polygon(50% 50%, 100% 50%, 75% 6.6%);
            background-color: black;
            transform: rotate(-90deg);
            align-self: center;
            margin-top: 0.7em;
            justify-self: center;
        }

        .song {
            position: relative;
            transform: rotate(180deg);
            margin-right: 1em;
            color: black;
            align-self: center;

            .artist {
                font-size: 0.6em;
            }
        }
    }
}

@keyframes move6 {
    0% {
        height: 0.2em;
    }

    25% {
        height: 0.7em;
    }

    50% {
        height: 1.5em;
    }

    100% {
        height: 0.2em;
    }
}
@primary-color: #6f91ee;@error-color: #ed0707;@body-background: #f0f0f0;